.page-main {
  width: 100%;
  margin: 0 auto;
  flex: 1 0 auto;

  & > h1 {
    text-align: left;
    @include title-h1;
    width: 1214px;
    margin: 64px auto 40px;
  }
}

.page-main__wrapper {
  width: 1214px;
  margin: 64px auto 122px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

@media (max-width: $tablet-max-width) {
  .page-main {
    h1 {
      @include title-tablet-h1;
      box-sizing: border-box;
      padding: 0 32px;
      width: 100%;
      max-width: $tablet-max-width;

      //width: 576px;
      margin: 40px auto 40px;
    }
  }

  .page-main__wrapper {
    width: 100%;
    max-width: $tablet-max-width;
    box-sizing: border-box;
    margin: 17px auto 64px;
    display: block;
  }
}

@media (max-width: $mobile-max-width) {
  .page-main {
    h1 {
      box-sizing: border-box;
      padding: 0 24px;
      width: 100%;
      max-width: $mobile-max-width;
      margin: 19px auto 16px;
    }
  }

  .page-main__wrapper {
    max-width: $mobile-max-width;
    margin: 17px auto 42px;
  }
}
